* {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  max-width: 100vw;
  max-height: 100vh;
  height: 100vh;
  width: 100vw;
}
.App .keyboard {
  display: flex;
  width: 100%;
  height: 100%;
}

.course {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.course button {
  width: 100%;
  height: 60px;
}

